"use client";

// React and Next
import { useEffect, useState } from "react";

// API
import { BackOrderCheckout } from "@/api/clientsideaction/buyerapi";

// Utils
import {
  EmailValidator,
  lowerPresent,
  numPresent,
  specialPresent,
  upperPresent,
} from "@/utilities/regex";

// 3rd Party
import Select from "react-select";
import { useRouter } from "nextjs-toploader/app";
// Redux
import { useDispatch, useSelector } from "react-redux";
import {
  loggedOutCheckoutObj,
  setLoggedOutAddressRedux,
} from "@/redux/slices/checkout";

// Components
import Contactus from "@/Components/Contactus";
import CheckoutPriceDetails from "../../CheckoutPriceDetails";
import { useTranslation } from "react-i18next";

export default function DeliveryAddress({
  token,
  countryList,
  stateList,
  addonlist,
}) {
  const dispatch = useDispatch();
  const router = useRouter();
  const { t } = useTranslation();
  const [validecheck, setValideCheck] = useState(false);
  const [emailError, setMailError] = useState("");
  const [country, setCountry] = useState("");
  const [btBuyNowLoader, setBtBuyNowLoader] = useState(false);
  const [createAccount, setCreateAccount] = useState(false);
  const [password, setPassWord] = useState("");
  const [passwordErrorShow, setPasswordErrorShow] = useState(false);
  const [passerror, setPassError] = useState("");
  const [passwordErrors, setPasswordErrors] = useState([]);
  const [passwordshow, setPasswordShow] = useState(false);
  const [addAddress, setAddress] = useState({
    email: "",
    cofEmail: "",
    name: "",
    address: "",
    landMark: "",
    city: "",
    pincode: "",
    number: "",
    lname: "",
  });
  const [countryOption, setCountryOption] = useState();
  const [statedependent, setStateDependent] = useState();
  const [stateOptions, setStateOptions] = useState();
  const [state, setState] = useState("");
  const [cartdata, setCartdata] = useState([]);
  const cartPath = useSelector((s) => s.CheckoutRedux.cartItems);
  const enableBackOrder = useSelector((s) => s.CheckoutRedux.enableBackOrder);
  const loggedOutAddressRedux = useSelector(
    (s) => s.CheckoutRedux.loggedOutAddressRedux
  );

  // cart
  useEffect(() => {
    const cartdatas = JSON.parse(localStorage.getItem("cartItems"));
    const cartdata = JSON.parse(localStorage.getItem("backOrder"));

    if (enableBackOrder) {
      setCartdata(cartdata);
    } else {
      setCartdata(cartdatas);
    }

    if (loggedOutAddressRedux?.loggedOutAddressAdded == true) {
      setAddress({ ...loggedOutAddressRedux?.addAddress });
      setCountry(loggedOutAddressRedux?.country);
      setState(loggedOutAddressRedux?.state);
      setPassWord(loggedOutAddressRedux?.password);
    }

    setCountryOption(countryList), setStateDependent(stateList);
  }, []);

  useEffect(() => {
    validate();

    dispatch(
      setLoggedOutAddressRedux({
        loggedOutAddressAdded: true,
        addAddress,
        country,
        state,
        password,
      })
    );
  }, [
    addAddress.email,
    addAddress.name,
    addAddress.landMark,
    addAddress.address,
    addAddress.number,
    addAddress.city,
    addAddress.pincode,
    country,
    state,
    password,
  ]);

  useEffect(() => {
    if (country != "" && statedependent != undefined) {
      const staoptionsget =
        statedependent &&
        statedependent?.filter(
          (option) => option?.country?.countryId === country?.value
        );
      const filterstate =
        staoptionsget &&
        staoptionsget?.map((data) => ({
          label: data.name,
          value: data.zoneId,
        }));
      setStateOptions(filterstate);
      const found = filterstate?.find((obj) => obj?.label === state?.label);
      if (found == undefined) {
        setState("");
      }
    }
  }, [country, state]);

  const handleinduscountry = (e) => {
    setState("");
    setCountry(e);
  };

  const handleChange = (e) => {
    const { name, value } = e.target;
    if (name === "number") {
      let regex = new RegExp(/[^0-9]/g);
      if (!value.match(regex)) {
        if (value.length < 16) {
          setAddress((pre) => ({
            ...pre,
            [name]: value,
          }));
        }
      }
    } else if (name == "pincode") {
      let regex = new RegExp(/[^0-9]/g);
      if (!value.match(regex)) {
        if (value.length < 7) {
          setAddress((pre) => ({
            ...pre,
            [name]: value?.trimStart(),
          }));
        }
      }
    } else {
      setAddress((pre) => ({
        ...pre,
        [name]: value,
      }));
    }
  };

  const validate = () => {
    let ValidateObj = {
      email: false,
      password: false,
    };
    if (addAddress.email == "") {
      setMailError(t("validationMessage.emailEmpty"));
      ValidateObj.email = false;
    } else {
      if (EmailValidator(addAddress.email)) {
        setMailError("");
        ValidateObj.email = true;
      } else {
        setMailError(t("validationMessage.incorrectEmail"));
        ValidateObj.email = false;
      }
    }

    if (password == "") {
      setPassError(t("validationMessage.passwordEmpty"));
      ValidateObj.password = false;
      setPasswordErrorShow(true);
    } else {
      let arrayValue = [];
      if (!upperPresent(password)) {
        arrayValue.push("Must contain at least 1 in Capital Case");
      } else {
        arrayValue.filter(
          (data) => data == "Must contain at least 1 in Capital Case"
        );
      }
      if (!numPresent(password)) {
        arrayValue.push("Must have at least 1 Number");
      } else {
        arrayValue.filter((data) => data == "Must have at least 1 Number");
      }
      if (!lowerPresent(password)) {
        arrayValue.push("Must contain at least 1 Lower Case");
      } else {
        arrayValue.filter(
          (data) => data == "Must contain at least 1 Lower Case"
        );
      }
      if (!specialPresent(password)) {
        arrayValue.push("Must contain at least 1 Special characters");
      } else {
        arrayValue.filter(
          (data) => data == "Must contain at least 1 Special characters"
        );
      }
      if (password.length <= 5) {
        arrayValue.push("Must be at least 6 characters");
      } else {
        arrayValue.filter((data) => data == "Must be at least 6 characters");
      }
      setPasswordErrors(arrayValue);
      if (arrayValue.length == 0) {
        setPassError("");
        ValidateObj.password = true;
        setPasswordErrorShow(true);
      } else {
        setPassError(
          "Incorrect Password. Please check the password you have entered."
        );
        ValidateObj.password = false;
        setPasswordErrorShow(true);
      }
    }
    if (createAccount) {
      if (
        addAddress.name != "" &&
        addAddress.landMark != "" &&
        addAddress.address != "" &&
        addAddress.number != "" &&
        addAddress.city != "" &&
        addAddress.pincode?.length == 6 &&
        country != "" &&
        state != "" &&
        ValidateObj.email &&
        ValidateObj.password
      ) {
        return true;
      } else {
        return false;
      }
    } else {
      if (
        addAddress.name != "" &&
        addAddress.landMark != "" &&
        addAddress.address != "" &&
        addAddress.number != "" &&
        addAddress.city != "" &&
        addAddress.pincode?.length == 6 &&
        country != "" &&
        state != "" &&
        ValidateObj.email
      ) {
        return true;
      } else {
        return false;
      }
    }
  };

  const handleEnterClick = (e) => {
    if (e.key === "Enter") {
      e.preventDefault();
      continePayment();
    }
  };

  const continePayment = () => {
    if (enableBackOrder) {
      const data = JSON.stringify({
        shippingFirstName: addAddress.name,
        shippingLastName: addAddress.lname,
        shippingCity: addAddress.city,
        shippingPostCode: addAddress.pincode,
        shippingZone: state.label,
        phoneNumber: addAddress.number,
        shippingAddress_1: addAddress.address,
        shippingAddress_2: addAddress.landMark,
        emailId: addAddress.email,
        shippingCountryId: country.value,
        productDetails: cartdata?.map((data) => ({
          ...data,
          quantity: data.productCount,
        })),
        paymentMethod: 1,
        shippingCompany: "",
      });
      setValideCheck(true);
      if (validate()) {
        setBtBuyNowLoader(true);
        setValideCheck(false);
        BackOrderCheckout(data, setBtBuyNowLoader, router);
      }
    } else {
      setValideCheck(true);
      if (validate()) {
        const obj = {
          addAddress: addAddress,
          country: country,
          state: state,
          cartdata: cartdata?.map((data) => ({
            ...data,
            quantity: data.productCount,
          })),
          createAccount: createAccount,
          password: password,
        };
        dispatch(loggedOutCheckoutObj(obj));
        setBtBuyNowLoader(true);
        setValideCheck(false);
        router?.push("/payment");
        setTimeout(() => {
          setBtBuyNowLoader(false);
        }, 2000);
        // Checkout(obj, dispatch, setBtBuyNowLoader, handlefunction);
      }
    }
  };

  // Design

  const colourStylesError = {
    control: (styles, state) => ({
      ...styles,
      cursor: "pointer",
      borderRadius: "0.25rem",
      height: "2.75rem",
      boxShadow: state.isFocused ? 0 : 0,
      borderColor: state.isFocused
        ? validecheck && country == ""
          ? "red"
          : ""
        : validecheck && country == ""
        ? "red"
        : "",
      "&:hover": {
        borderColor: state.isFocused
          ? ""
          : validecheck && country == ""
          ? "red"
          : "",
      },
    }),
    option: (base, state) => ({
      ...base,
      cursor: "pointer",
      backgroundColor: state.isSelected ? "#ffffff" : "#ffffff",

      "&:hover": { backgroundColor: "#F9F9FC" },

      fontSize: ".8125rem",
      padding: "8px",
      color: "#333333",
    }),
  };

  const colourStylesErrorState = {
    control: (styles, statelist) => ({
      ...styles,
      cursor: "pointer",
      borderRadius: "0.25rem",
      height: "2.75rem",
      boxShadow: statelist.isFocused ? 0 : 0,
      borderColor: statelist.isFocused
        ? validecheck && state == ""
          ? "red"
          : ""
        : validecheck && state == ""
        ? "red"
        : "",
      "&:hover": {
        borderColor: statelist.isFocused
          ? ""
          : validecheck && state == ""
          ? "red"
          : "",
      },
    }),
    option: (base, statelist) => ({
      ...base,
      cursor: "pointer",
      backgroundColor: statelist.isSelected ? "#ffffff" : "#ffffff",

      "&:hover": { backgroundColor: "#F9F9FC" },

      fontSize: ".8125rem",
      padding: "8px",
      color: "#333333",
    }),
  };

  return (
    <>
      <div className="max-w-screen-xl px-4 py-8 m-auto mb-20 sm:px-10">
        <h3 className="mb-[10px] p-[20px_0] font-semibold text-[#202124] text-[20px] leading-[24px]">
          {t("checkout.DelAddr")}
        </h3>
        <div className="gap-[60px] grid grid-cols-[1fr_auto]">
          <div className="w-full mx-auto">
            <div className="gap-x-[20px] gap-y-[25px] grid grid-cols-2 max-sm:grid-cols-1">
              {/* Email ID */}
              <div className="col-span-2 max-sm:col-span-1">
                <input
                  id="exampleFormControlInput1"
                  placeholder={t("signin.EmailId")}
                  value={addAddress?.email?.trimStart()}
                  name="email"
                  onChange={handleChange}
                  onKeyDown={(e) => handleEnterClick(e)}
                  className={`p-[15.5px_20px] border border-[#BEC0C5] focus:!border-[#BEC0C5] border-solid rounded-[8px] w-full h-[48px] font-normal text-[#202124] text-[14px] placeholder:text-[#737782] focus-visible:outline-none ${
                    validecheck == true &&
                    addAddress.email == "" &&
                    "border-valid"
                  }`}
                />
                {validecheck && addAddress.email == "" && (
                  <p className="mt-1 text-xs font-normal text-red-invalid">
                    {emailError}
                  </p>
                )}
              </div>
              {/* First name */}
              <div>
                <input
                  type="text"
                  placeholder={t("signin.FirstName")}
                  value={addAddress.name.trimStart()}
                  name="name"
                  maxLength={32}
                  onChange={handleChange}
                  onKeyDown={(e) => handleEnterClick(e)}
                  id="exampleFormControlInput2"
                  className={`p-[15.5px_20px] border border-[#BEC0C5] focus:!border-[#BEC0C5] border-solid rounded-[8px] w-full h-[48px] font-normal text-[#202124] text-[14px] placeholder:text-[#737782] ${
                    validecheck == true &&
                    addAddress.name == "" &&
                    "border-valid"
                  }`}
                />
                {validecheck && addAddress.name == "" && (
                  <p className="mt-1 text-xs font-normal text-red-invalid">
                    {t("checkout.Entname")}
                  </p>
                )}
              </div>
              {/* Last name */}
              <div>
                <input
                  type="text"
                  value={addAddress.lname.trimStart()}
                  name="lname"
                  maxLength={32}
                  onChange={handleChange}
                  onKeyDown={(e) => handleEnterClick(e)}
                  id="exampleFormControlInput3"
                  placeholder={t("signin.LastName")}
                  className="p-[15.5px_20px] border border-[#BEC0C5] focus:!border-[#BEC0C5] border-solid rounded-[8px] w-full h-[48px] font-normal text-[#202124] text-[14px] placeholder:text-[#737782]"
                />
              </div>
              {/* Mobile no. */}
              <div className="col-span-2 max-sm:col-span-1">
                <input
                  type="text"
                  id="exampleFormControlInput4"
                  name="number"
                  onChange={handleChange}
                  onKeyDown={(e) => handleEnterClick(e)}
                  placeholder={t("checkout.Mobile")}
                  value={addAddress?.number}
                  className={`p-[15.5px_20px] border border-[#BEC0C5] focus:!border-[#BEC0C5] border-solid rounded-[8px] w-full h-[48px] font-normal text-[#202124] text-[14px] placeholder:text-[#737782] ${
                    validecheck == true &&
                    addAddress.number == "" &&
                    "border-valid"
                  }`}
                />
                {validecheck && addAddress.number == "" && (
                  <p className="mt-1 text-xs font-normal text-red-invalid">
                    {t("checkout.EntNum")}
                  </p>
                )}
              </div>
              {/* Street address */}
              <div className="col-span-2 max-sm:col-span-1">
                <textarea
                  id="exampleFormControlInput5"
                  value={addAddress.address.trimStart()}
                  name="address"
                  maxLength={128}
                  onChange={handleChange}
                  onKeyDown={(e) => handleEnterClick(e)}
                  placeholder={t("checkout.StrAddr")}
                  className={`p-[15.5px_20px] border border-[#BEC0C5] focus:!border-[#BEC0C5] border-solid rounded-[8px] w-full h-[119px] font-normal text-[#202124] text-[14px] placeholder:text-[#737782] resize-none ${
                    validecheck == true &&
                    addAddress.address == "" &&
                    "border-valid"
                  }`}
                ></textarea>
                {validecheck && addAddress?.address == "" && (
                  <p className="mt-1 text-xs font-normal text-red-invalid">
                    {t("checkout.youraddr")}
                  </p>
                )}
              </div>
              {/* Country */}
              <div className="cursor-pointer">
                <Select
                  name="select"
                  options={countryOption}
                  onChange={(e) => handleinduscountry(e)}
                  styles={colourStylesError}
                  value={country}
                  placeholder={t("myprofile.Country")}
                  isClearable
                  classNamePrefix="react-select"
                  className={`block w-full px-0 mt-0 bg-transparent border-0  appearance-none z-1 focus:outline-none focus:ring-0 focus:border-black border-gray-200 ${
                    validecheck == true && country == ""
                      ? "border-valid"
                      : "react-select-container"
                  }`}
                />
                {validecheck && country == "" && (
                  <p className="mt-1 text-xs font-normal text-red-invalid">
                    {t("checkout.SelCountry")}
                  </p>
                )}
              </div>
              {/* State */}
              <div className="cursor-pointer">
                <Select
                  name="select"
                  options={stateOptions}
                  onChange={(e) => {
                    setState(e);
                  }}
                  value={state}
                  styles={colourStylesErrorState}
                  classNamePrefix="react-select"
                  className={`block w-full px-0 mt-0 bg-transparent border-0  appearance-none z-1 focus:outline-none focus:ring-0 focus:border-black border-gray-200 cursor-pointer ${
                    validecheck == true && state == ""
                      ? "border-valid"
                      : "react-select-container"
                  }`}
                  placeholder={t("myprofile.State")}
                />
                {validecheck && state == "" && (
                  <p className="mt-1 text-xs font-normal text-red-invalid">
                    {t("checkout.State")}
                  </p>
                )}
              </div>
              {/* City */}
              <div>
                <input
                  type="text"
                  value={addAddress?.city?.trimStart()}
                  name="city"
                  maxLength={128}
                  onChange={handleChange}
                  onKeyDown={(e) => handleEnterClick(e)}
                  id="exampleFormControlInput8"
                  placeholder={t("myprofile.City")}
                  className={`p-[15.5px_20px] border border-[#BEC0C5] focus:!border-[#BEC0C5] border-solid rounded-[8px] w-full h-[48px] font-normal text-[#202124] text-[14px] placeholder:text-[#737782] ${
                    validecheck == true &&
                    addAddress.city == "" &&
                    "border-valid"
                  }`}
                />

                {validecheck && addAddress?.city == "" && (
                  <p className="mt-1 text-xs font-normal text-red-invalid">
                    {t("checkout.Entcity")}
                  </p>
                )}
              </div>
              {/* Area street sector */}
              {/* <div>
                  <input
                    type="text"
                    name=""
                    id=""
                    placeholder="Area, Street, Sector, Village*"
                    className="p-[15.5px_20px] border border-[#BEC0C5] focus:!border-[#BEC0C5] border-solid rounded-[8px] w-full h-[48px] font-normal text-[#202124] text-[14px] placeholder:text-[#737782]"
                  />
                </div> */}
              {/* Pincode */}
              <div>
                <input
                  type="number"
                  id="exampleFormControlInput10"
                  value={addAddress.pincode.trimStart()}
                  name="pincode"
                  onChange={handleChange}
                  onKeyDown={(e) => handleEnterClick(e)}
                  onWheel={(e) => e.target.blur()}
                  placeholder={t("checkout.EntPin")}
                  className={`p-[15.5px_20px] border border-[#BEC0C5] focus:!border-[#BEC0C5] border-solid rounded-[8px] w-full h-[48px] font-normal text-[#202124] text-[14px] placeholder:text-[#737782] ${
                    validecheck == true && addAddress?.pincode == ""
                      ? "border-valid"
                      : addAddress?.pincode?.length !== 6 &&
                        validecheck == true &&
                        "border-valid"
                  }`}
                />

                {validecheck && addAddress.pincode == "" && (
                  <p className="mt-1 text-xs font-normal text-red-invalid">
                    {t("checkout.EntPin")}
                  </p>
                )}

                {validecheck &&
                  addAddress?.pincode !== "" &&
                  addAddress?.pincode?.length !== 6 && (
                    <p className="mt-1 text-xs font-normal text-red-invalid">
                      {t("validationMessage.pincodeLength")}
                    </p>
                  )}
              </div>
              {/* Apt suite landmark */}
              <div className="col-span-2 max-sm:col-span-1">
                <input
                  type="text"
                  value={addAddress.landMark.trimStart()}
                  name="landMark"
                  maxLength={128}
                  onChange={handleChange}
                  onKeyDown={(e) => handleEnterClick(e)}
                  id="exampleFormControlInput9"
                  placeholder={t("checkout.optional")}
                  className={`p-[15.5px_20px] border border-[#BEC0C5] focus:!border-[#BEC0C5] border-solid rounded-[8px] w-full h-[48px] font-normal text-[#202124] text-[14px] placeholder:text-[#737782] ${
                    validecheck == true &&
                    addAddress.landMark == "" &&
                    "border-valid"
                  }`}
                />

                {validecheck && addAddress?.landMark == "" && (
                  <p className="mt-1 text-xs font-normal text-red-invalid">
                    {t("checkout.youraddr")}
                  </p>
                )}
              </div>
              {/* Create account */}
              <div className="flex items-center col-span-2 space-x-2 max-sm:col-span-1">
                <label
                  htmlFor="setDefault"
                  className="flex items-center gap-[10px] font-normal text-[#202124] text-base cursor-pointer"
                >
                  <input
                    id="setDefault"
                    type="checkbox"
                    checked={createAccount}
                    onChange={(e) => setCreateAccount(e.target.checked)}
                    className="hidden w-8 h-8 text-white rounded-md cursor-pointer peer border-grey-400 focus:ring-0 focus:ring-transparent checkbox"
                  />
                  <img
                    src="/img/unchecked.svg"
                    alt=""
                    className="block peer-checked:hidden"
                    height={"18px"}
                    width={"18px"}
                  />
                  <img
                    src="/img/checked.svg"
                    alt=""
                    className="hidden peer-checked:block"
                    height={"18px"}
                    width={"18px"}
                  />
                  {t("signin.CreateAccount")}
                </label>
              </div>

              {/* Create account password*/}
              {createAccount && (
                <div className="relative col-span-2 mb-3 max-sm:col-span-1">
                  <form
                    autoComplete="off"
                    onSubmit={(e) => {
                      e && e.preventDefault();
                    }}
                  >
                    <input
                      type={passwordshow ? "text" : "password"}
                      value={password}
                      onChange={(e) => setPassWord(e.target.value.trimStart())}
                      onBlur={(e) => setPasswordErrorShow(true)}
                      onFocus={(e) => setPasswordErrorShow(false)}
                      onKeyDown={(e) => handleEnterClick(e)}
                      className={`pe-[50px] p-[15.5px_20px] border border-[#BEC0C5] focus:!border-[#BEC0C5] border-solid rounded-[8px] w-full h-[48px] font-normal text-[#202124] text-[14px] placeholder:text-[#737782] ${
                        passerror == "" && password !== ""
                          ? "border-sucess"
                          : password === "" &&
                            passwordErrorShow == true &&
                            passerror &&
                            "border-valid"
                      }`}
                      id="exampleFormControlInput11"
                      placeholder={t("signin.EnterPassword")}
                    />
                  </form>

                  {validecheck && passerror !== "" && password == "" && (
                    <p className="mt-1 text-xs font-normal text-red-invalid">
                      {passerror}
                    </p>
                  )}
                  {password !== "" && passwordErrorShow && (
                    <div className="mt-2">
                      <ul className="grid grid-cols-1 list-disc gap-y-2 sm:grid-cols-2 ps-7">
                        <li
                          className={`text-xs font-normal ${
                            passwordErrors.includes(
                              "Must contain at least 1 in Capital Case"
                            )
                              ? "text-grey-400"
                              : "text-activegreen"
                          }`}
                        >
                          {t("validationMessage.CapitalCase")}
                        </li>
                        <li
                          className={`text-xs font-normal ${
                            passwordErrors.includes(
                              "Must have at least 1 Number"
                            )
                              ? "text-grey-400"
                              : "text-activegreen"
                          }`}
                        >
                          {t("validationMessage.Number")}
                        </li>
                        <li
                          className={`text-xs font-normal ${
                            passwordErrors.includes(
                              "Must contain at least 1 Lower Case"
                            )
                              ? "text-grey-400"
                              : "text-activegreen"
                          }`}
                        >
                          {t("validationMessage.LowerCase")}
                        </li>
                        <li
                          className={`text-xs font-normal ${
                            passwordErrors.includes(
                              "Must be at least 6 characters"
                            )
                              ? "text-grey-400"
                              : "text-activegreen"
                          }`}
                        >
                          {t("validationMessage.Characters")}
                        </li>
                        <li
                          className={`text-xs font-normal ${
                            passwordErrors.includes(
                              "Must contain at least 1 Special characters"
                            )
                              ? "text-grey-400"
                              : "text-activegreen"
                          }`}
                        >
                          {t("validationMessage.SpecChars")}
                        </li>
                      </ul>
                    </div>
                  )}
                  <a
                    role="button"
                    className="absolute flex items-center space-x-3 text-sm font-medium cursor-pointer top-3 right-3 text-black-300"
                    onClick={() => setPasswordShow(!passwordshow)}
                  >
                    {passwordshow ? t("signin.Hide") : t("signin.Show")}
                  </a>
                </div>
              )}
            </div>
          </div>

          {/* Price Details */}
          <CheckoutPriceDetails
            token={token}
            handlePayNow={continePayment}
            cartPath={cartPath}
            addonlist={addonlist}
            loader={btBuyNowLoader}
            valid={true}
          />
        </div>
      </div>

      <Contactus token={token} />
    </>
  );
}
