// React and Next
import { Fragment, useEffect, useState } from "react";

// API
import { DefaultAddressApi } from "@/api/clientsideaction/profileapi";
import { AddressListApi } from "@/api/clientsideaction/buyerapi";
import {
  BackOrderCheckout,
  BuyerAddAddressCheckout,
  CheckoutEditAddress,
} from "@/api/clientsideaction/buyerapi";
import { MyprofileAddressDelete } from "@/api/clientsideaction/profileapi";

// Utils
import { backOrderPaymentMethod } from "@/utilities/url";

// 3rd Party
import { useRouter } from "nextjs-toploader/app";
// Redux
import { useDispatch, useSelector } from "react-redux";
import {
  selectedBillingAddress,
  selectedShippingAddress,
  setDeliveryAsBillingAddressRedux,
} from "@/redux/slices/checkout";

// Components
import AddNewAddress from "./AddNewAddress";
import CheckoutPriceDetails from "../../CheckoutPriceDetails";
import Contactus from "@/Components/Contactus";
import DeliveryBillingAddressSkeleton from "./skeleton";
import { useTranslation } from "react-i18next";

export default function DeliveryBillingAddress({
  token,
  countryList,
  stateList,
  addonlist,
}) {
  const dispatch = useDispatch();
  const router = useRouter();
  const { t } = useTranslation();
  const [shippingAddressList, setShippingAddressList] = useState([]);
  const [billingAddressList, setBillingAddressList] = useState([]);
  const [defaultShippingAddress, setDefaultShippingAddress] = useState();
  const [defaultbillingAddress, setDefaultBillingAddress] = useState();
  const [billToSameAddress, setBillToSameAddress] = useState(false);
  const [delivery, setDelivery] = useState(false);
  const [addAddress, setAddress] = useState({
    name: "",
    address: "",
    address2: "",
    landMark: "",
    city: "",
    pincode: "",
    number: "",
    country: {},
    state: {},
    lname: "",
  });
  const [validecheck, setValideCheck] = useState(false);
  const [country, setCountry] = useState("");
  const [newAddressType, setNewAddressType] = useState(null);
  const [addressType, setAddressType] = useState();
  const [loader, setLoader] = useState(false);
  const [isAddressesLoading, setIsAddressesLoading] = useState(false);
  const [countryOption, setCountryOption] = useState();
  const [statedependent, setStateDependent] = useState();
  const [stateOptions, setStateOptions] = useState();
  const [state, setState] = useState("");
  const [cartdata, setCartdata] = useState([]);
  const [shippingAddressData, setShippingAddressData] = useState([]);
  const [billingAddressData, setBillingAddressData] = useState([]);
  const [triger, setTriger] = useState(0);
  const [type, setType] = useState("add");
  const [stateTrigger, setStateTigger] = useState(0);
  const [defalut, setDefault] = useState(false);
  const CartData = useSelector((state) => state.userReducer.cardpopCount);
  const cartPath = useSelector((s) => s.CheckoutRedux.cartItems);
  const enableBackOrder = useSelector((s) => s.CheckoutRedux.enableBackOrder);
  const deliveryAsBillingAddressRedux = useSelector(
    (s) => s.CheckoutRedux.deliveryAsBillingAddressRedux
  );

  useEffect(() => {
    const cartdatas = JSON.parse(localStorage.getItem("cartItems"));
    const cartdata = JSON.parse(localStorage.getItem("backOrder"));

    if (enableBackOrder) {
      setCartdata(cartdata);
    } else {
      setCartdata(cartdatas);
    }
  }, []);

  useEffect(() => {
    setIsAddressesLoading(true);
    AddressListApi(handleFilterAddress);
  }, []);

  useEffect(() => {
    if (defaultShippingAddress && defaultbillingAddress) {
      if (
        defaultShippingAddress?.firstName == defaultbillingAddress?.firstName &&
        defaultShippingAddress?.lastName == defaultbillingAddress?.lastName &&
        defaultShippingAddress?.address1 == defaultbillingAddress?.address1 &&
        defaultShippingAddress?.address2 == defaultbillingAddress?.address2 &&
        defaultShippingAddress?.city == defaultbillingAddress?.city &&
        defaultShippingAddress?.postcode == defaultbillingAddress?.postcode
      ) {
        setBillToSameAddress(true);
      } else {
        setBillToSameAddress(false);
      }
    } else {
      setBillToSameAddress(false);
    }
  }, [defaultShippingAddress, defaultbillingAddress]);

  useEffect(() => {
    setCountryOption(countryList), setStateDependent(stateList);
    setShippingAddressData(shippingAddressList);
    setBillingAddressData(billingAddressList);
  }, [countryList, stateList, shippingAddressList, billingAddressList]);

  useEffect(() => {
    if (country != "" && statedependent != undefined) {
      const staoptionsget =
        statedependent &&
        statedependent?.filter(
          (option) => option?.country?.countryId === country?.value
        );
      const filterstate =
        staoptionsget &&
        staoptionsget?.map((data) => ({
          label: data.name,
          value: data.zoneId,
        }));
      setStateOptions(filterstate);
      const found = filterstate?.find((obj) => obj?.label === state?.label);
      if (found == undefined) {
        setState("");
      }
    }
  }, [country, state]);

  useEffect(() => {
    if (deliveryAsBillingAddressRedux) {
      setDefaultBillingAddress(defaultShippingAddress);
      setBillToSameAddress(true);
    }
  }, [defaultShippingAddress]);

  useEffect(() => {
    if (shippingAddressData) {
      const found = shippingAddressData?.find((obj) => obj?.isDefault === 1);
      handleDefaultShippingCheck(found);
    }

    if (billingAddressData) {
      const found = billingAddressData?.find((obj) => obj?.isDefault === 1);
      handleDefaultBillingCheck(found);
    }
  }, [shippingAddressData, billingAddressData]);

  const handleFilterAddress = (data) => {
    let addList = data.filter((s) => s.addressType == 0);
    let billaddressList = data.filter((s) => s.addressType == 1);
    if (addList) {
      const defaultAddress = addList.find((address) => address?.isDefault == 1);
      if (defaultAddress) {
        setDefaultShippingAddress(defaultAddress);
      } else {
        setDefaultShippingAddress(null);
      }
      setShippingAddressList(addList);
    }
    if (billaddressList) {
      setBillingAddressList(billaddressList);
      const defaultAddress = billaddressList.find(
        (address) => address?.isDefault == 1
      );
      if (defaultAddress) {
        setDefaultBillingAddress(defaultAddress);
      } else {
        setDefaultBillingAddress(null);
      }
    }
    setIsAddressesLoading(false);
  };

  const handleAddress = () => {
    AddressListApi(handleFilterAddress);
  };

  const handleDefaultShippingAddressChange = (data, type) => {
    setDefaultShippingAddress(data);
    if (type == "radio") {
      DefaultAddressApi(data?.addressId, 0, handleAddress);
    }
    if (billToSameAddress) {
      setDefaultBillingAddress(data);
    }
  };

  const handleDefaultBillingAddressChange = (data, type) => {
    setDefaultBillingAddress(data);
    if (type == "radio") {
      DefaultAddressApi(data?.addressId, 1, handleAddress);
    }
  };

  const handleBillToSameAddress = (e) => {
    if (e.target.checked) {
      setDefaultBillingAddress(defaultShippingAddress);
    } else {
      const defaultAddress = billingAddressList.find(
        (address) => address?.isDefault == 1
      );
      setDefaultBillingAddress(defaultAddress);
    }
    setBillToSameAddress(e.target.checked);
    dispatch(setDeliveryAsBillingAddressRedux(true));
  };

  const setdelivery = (num) => {
    setNewAddressType(num);
    setDelivery(true);
    setDefault(false);
    setAddress({
      name: "",
      address: "",
      address2: "",
      landMark: "",
      city: "",
      pincode: "",
      number: "",
      country: {},
      state: {},
      editId: "",
      lname: "",
    });
    setState("");
    setStateOptions();

    setCountry("");

    setType("add");
  };

  const validate = () => {
    if (
      addAddress?.name?.length &&
      addAddress?.number?.toString()?.length &&
      addAddress?.address?.length &&
      country?.label &&
      state?.label &&
      addAddress?.city?.length &&
      addAddress?.pincode?.toString()?.length == 6 &&
      addAddress?.address2?.length
    ) {
      return true;
    } else {
      return false;
    }
  };

  const handleEdit = (data) => {
    setDefault(data.isDefault == 1 ? true : false);
    setDelivery(true);
    setAddress({
      name: data.firstName,
      address: data.address1,
      address2: data.address2,
      landMark: data.landmark,
      city: data.city,
      pincode: data.postcode?.toString(),
      number: data.phoneNo,
      country: {},
      state: {},
      lname: data?.lastName,
      editId: data.addressId,
    });
    setAddressType(data?.addressType);
    let cntryName = countryList?.find((s) => s.value == data?.countryId);
    if (cntryName) {
      setCountry(cntryName);
    }
    setState({ label: data?.zone?.name, value: data?.zone?.zoneId });
    setType("Edit");
  };

  const handleClose = () => {
    setDelivery(false);
    setTriger(triger + 1);
    setValideCheck(false);
  };

  const handleAddressSubmit = () => {
    addAddress.country = country;
    addAddress.state = state;
    addAddress.defalut = defalut;
    setValideCheck(true);
    if (validate()) {
      setIsAddressesLoading(true);
      setValideCheck(false);
      if (type === "add") {
        BuyerAddAddressCheckout(
          addAddress,
          handleClose,
          newAddressType,
          handleAddress
        );
      } else {
        CheckoutEditAddress(
          addAddress,
          handleClose,
          addressType,
          handleAddress
        );
      }
    }
  };
  const hadleCounty = (data) => {
    let cntryName = countryOption?.find((s) => s.value == data.countryId);
    if (cntryName) {
      return cntryName.label;
    }
  };
  const handleDelete = (data) => {
    MyprofileAddressDelete(data.addressId, triger, setTriger, handleAddress);
  };

  const handleDefaultShippingCheck = (data) => {
    shippingAddressData?.map((sdata) => {
      if (sdata.addressId == data?.addressId) {
        sdata.isDefault = 1;
      } else {
        sdata.isDefault = 0;
      }
    });
    setAddress({
      name: data?.firstName,
      lname: data?.lastName,
      address: data?.address1,
      address2: data?.address2,
      landMark: data?.landmark,
      city: data?.city,
      pincode: data?.postcode?.toString(),
      number: data?.phoneNo,
      country: {},
      state: {},
      editId: data?.addressId,
    });
    let cntryName = countryList?.find((s) => s?.value == data?.countryId);
    if (cntryName) {
      setCountry(cntryName);
    }
    setState({ label: data?.zone?.name, value: data?.zone?.zoneId });
    setStateTigger(stateTrigger + 1);
  };

  const handleDefaultBillingCheck = (data) => {
    billingAddressData?.map((sdata) => {
      if (sdata.addressId == data?.addressId) {
        sdata.isDefault = 1;
      } else {
        sdata.isDefault = 0;
      }
    });
    setAddress({
      name: data?.firstName,
      lname: data?.lastName,
      address: data?.address1,
      address2: data?.address2,
      landMark: data?.landmark,
      city: data?.city,
      pincode: data?.postcode?.toString(),
      number: data?.phoneNo,
      country: {},
      state: {},
      editId: data?.addressId,
    });
    let cntryName = countryList?.find((s) => s?.value == data?.countryId);
    if (cntryName) {
      setCountry(cntryName);
    }
    setState({ label: data?.zone?.name, value: data?.zone?.zoneId });
    setStateTigger(stateTrigger + 1);
  };

  const handlePayNow = () => {
    if (enableBackOrder) {
      if (defaultShippingAddress) {
        setLoader(true);
        let email = localStorage.getItem("email");
        const data = JSON.stringify({
          shippingFirstName: defaultShippingAddress?.name
            ? defaultShippingAddress.name
            : defaultShippingAddress?.firstName,
          shippingLastName: defaultShippingAddress?.lname
            ? defaultShippingAddress.lname
            : defaultShippingAddress?.lastName,
          shippingCity: defaultShippingAddress.city,
          shippingPostCode: defaultShippingAddress?.pincode
            ? defaultShippingAddress?.pincode
            : defaultShippingAddress?.postcode,
          shippingZone: defaultShippingAddress?.zone?.name,
          phoneNumber: defaultShippingAddress?.number
            ? defaultShippingAddress.number
            : defaultShippingAddress?.phoneNo,
          shippingAddress_1: defaultShippingAddress?.address
            ? defaultShippingAddress.address
            : defaultShippingAddress.address1,
          shippingAddress_2: defaultShippingAddress.address2,
          emailId: email,
          shippingCountryId: defaultShippingAddress?.countryId,
          productDetails: cartdata?.map((data) => ({
            ...data,
            quantity: data.productCount,
          })),
          paymentMethod: backOrderPaymentMethod,
          shippingCompany: defaultShippingAddress?.name
            ? defaultShippingAddress.name
            : defaultShippingAddress?.firstName,
        });
        BackOrderCheckout(data, setLoader, router);
      }
    } else {
      if (defaultShippingAddress && defaultbillingAddress) {
        dispatch(selectedShippingAddress(defaultShippingAddress));
        dispatch(selectedBillingAddress(defaultbillingAddress));
        router?.push("/payment");
        setTimeout(() => {
          setLoader(false);
        }, 2000);
      }
    }
  };

  const isPhysicalProductExist = CartData?.some(
    (type) => type?.productType == "physical"
  );

  return (
    <>
      {!delivery ? (
        <div className="max-w-screen-xl px-4 py-8 m-auto mb-20 sm:px-10">
          {isPhysicalProductExist &&
            (isAddressesLoading ? (
              <div className="w-[200px] h-[20px] bg-gray-300 rounded mb-7"></div>
            ) : (
              <h2 className="mb-7 font-semiblod text-[#202124] text-[20px] leading-[24px]">
                {t("checkout.DelAddr")}
              </h2>
            ))}
          <div className="gap-[60px] grid grid-cols-[1fr_auto] max-md:grid-cols-1">
            {isAddressesLoading ? (
              <DeliveryBillingAddressSkeleton />
            ) : (
              <div className="">
                {/* Delivery Address */}

                {isPhysicalProductExist && (
                  <Fragment>
                    {shippingAddressData != "" && (
                      <div className="p-[30px_20px] border border-[#E8E8E8] border-solid rounded-[12px]">
                        {shippingAddressData?.map((data, i) => (
                          <div
                            className="flex justify-between items-start space-x-3 rtl:space-x-[unset] mb-[30px] last-of-type:mb-0 pb-[30px] last-of-type:pb-0 border-[#F4F4F4] border-b last-of-type:border-b-0 border-solid"
                            key={i}
                          >
                            <div className="flex items-start space-x-2 rtl:ml-3">
                              <input
                                id="1"
                                onChange={() =>
                                  handleDefaultShippingAddressChange(
                                    data,
                                    "radio"
                                  )
                                }
                                type="radio"
                                className="peer top-1 relative bg-white checked:!bg-[#000000] bg-contain bg-no-repeat checked:bg-none bg-center m-0 border !border-white checked:!border-white checked:border-[0.1875rem] rounded-full outline checked:outline outline-[#CCCCCC] outline-1 checked:!outline-[#000000] checked:outline-[2px] !outline-offset-0 focus:ring-0 w-[15px] checked:w-[16px] min-w-[18px] checked:min-w-[13px] h-[18px] checked:h-[16px] align-top appearance-none cursor-pointer print-color-auto"
                                checked={data?.isDefault == 1 ? true : false}
                              />

                              <label
                                onClick={() =>
                                  handleDefaultShippingAddressChange(
                                    data,
                                    "radio"
                                  )
                                }
                                htmlFor="1"
                                className="text-sm font-normal cursor-pointer text-black-300"
                              >
                                <div className="flex flex-col items-start gap-[7px]">
                                  {data.isDefault == 1 && (
                                    <div className="flex justify-center items-center bg-[#ECF2FE] mb-[10px] px-[10px] rounded w-auto h-[27px] font-medium text-[#164092] text-[14px] leading-[17px]">
                                      {t("checkout.Default")}
                                    </div>
                                  )}

                                  <div className="flex flex-col space-y-px">
                                    <p className="font-semibold text-[#202124] text-sm leading-[22px]">
                                      {data?.firstName}
                                    </p>
                                    <p className="font-normal text-[#202124] text-sm break-all leading-[22px]">
                                      {data?.address1}, {data?.address2} ,{" "}
                                      {data?.city}-{data?.postcode} ,{" "}
                                      {data?.zone?.name} , {hadleCounty(data)}
                                    </p>
                                    <p className="font-semibold text-[#202124] text-sm leading-[22px]">
                                      <span>{t("checkout.phone")}</span>
                                      <span>+91 {data?.phoneNo}</span>
                                    </p>
                                  </div>
                                </div>
                              </label>
                            </div>
                            <div className="flex flex-col space-y-6 max-w-[167px]">
                              <div className="flex flex-row items-center justify-between gap-3">
                                <a
                                  role="button"
                                  onClick={() => handleEdit(data)}
                                  className="relative flex items-center space-x-1 p-[8.5px_12px] border border-[#D9D9D9] border-solid rounded-[4px] min-w-[68px] font-normal text-[#202124] text-[14px] leading-none cursor-pointer"
                                >
                                  <img src="/img/edit.svg" alt="edit" />
                                  <span> {t("myprofile.Edit")}</span>
                                </a>
                                <a
                                  role="button"
                                  onClick={() => handleDelete(data)}
                                  className="relative flex items-center space-x-1 p-[8.5px_12px] border border-[#D9D9D9] border-solid rounded-[4px] min-w-[83px] font-normal text-[#202124] text-[14px] leading-none cursor-pointer"
                                >
                                  <img src="/img/delete.svg" alt="delete" />
                                  <span> {t("myprofile.Delete")}</span>
                                </a>
                              </div>
                            </div>
                          </div>
                        ))}
                      </div>
                    )}
                    {shippingAddressData != "" ? (
                      <a
                        role="button"
                        className={`text-[#202124CC] text-[14px] flex justify-center gap-[10px] items-center font-medium leading-4 mt-[16px] capitalize cursor-pointer border border-[#C2C2C2] p-[14px] rounded-[8px] text-center border-dashed `}
                        onClick={() => setdelivery(0)}
                      >
                        <img src="/img/add.svg" alt="add" />
                        {t("checkout.NewAddr")}
                      </a>
                    ) : (
                      <a
                        role="button"
                        className={`text-[#202124CC] h-[235px] text-[18px] flex flex-col justify-center gap-[8px] items-center font-medium leading-[22px] capitalize cursor-pointer border border-[#C2C2C2] p-[14px] rounded-[8px] text-center border-dashed `}
                        onClick={() => setdelivery(0)}
                      >
                        <img src="/img/add-new-address.svg" alt="add" />
                        {t("checkout.NewAddr")}
                      </a>
                    )}
                  </Fragment>
                )}

                {enableBackOrder == false && (
                  <Fragment>
                    {/* Billing Address */}
                    <h2 className="flex justify-between items-start my-7 font-semiblod text-[#202124] text-[20px] leading-[24px]">
                      {`${t("chooseAddress.selectBillAddr")}`}

                      {defaultShippingAddress == null ? (
                        <></>
                      ) : (
                        <div className="flex items-center space-x-2">
                          {isPhysicalProductExist && (
                            <label
                              htmlFor="billtosameaddress"
                              className="flex items-center gap-[10px] font-normal text-[#202124] text-base cursor-pointer"
                            >
                              <input
                                type="checkbox"
                                className="hidden w-8 h-8 text-white rounded-md cursor-pointer peer border-grey-400 focus:ring-0 focus:ring-transparent checkbox"
                                checked={billToSameAddress}
                                id="billtosameaddress"
                                onChange={(e) => handleBillToSameAddress(e)}
                              />

                              <img
                                src="/img/unchecked.svg"
                                alt=""
                                className="block peer-checked:hidden"
                                height={"18px"}
                                width={"18px"}
                              />
                              <img
                                src="/img/checked.svg"
                                alt=""
                                className="hidden peer-checked:block"
                                height={"18px"}
                                width={"18px"}
                              />
                              {t("checkout.SetDelivAddr")}
                            </label>
                          )}
                        </div>
                      )}
                    </h2>

                    {billToSameAddress ? (
                      defaultbillingAddress ? (
                        <div className="p-[30px_20px] border border-[#E8E8E8] border-solid rounded-[12px]">
                          <div className="flex justify-between items-start space-x-3 rtl:space-x-[unset] mb-[30px] last-of-type:mb-0 pb-[30px] last-of-type:pb-0 border-[#F4F4F4] border-b last-of-type:border-b-0 border-solid">
                            <div className="flex items-start space-x-2 rtl:ml-3">
                              <input
                                id="1"
                                onChange={() =>
                                  handleDefaultBillingAddressChange(
                                    data,
                                    "radio"
                                  )
                                }
                                type="radio"
                                className="peer top-1 relative bg-white checked:!bg-[#000000] bg-contain bg-no-repeat checked:bg-none bg-center m-0 border !border-white checked:!border-white checked:border-[0.1875rem] rounded-full outline checked:outline outline-[#CCCCCC] outline-1 checked:!outline-[#000000] checked:outline-[2px] !outline-offset-0 focus:ring-0 w-[15px] checked:w-[16px] min-w-[18px] checked:min-w-[13px] h-[18px] checked:h-[16px] align-top appearance-none cursor-pointer print-color-auto"
                                checked={
                                  defaultbillingAddress?.isDefault == 1
                                    ? true
                                    : false
                                }
                              />

                              <label
                                onClick={() =>
                                  handleDefaultBillingAddressChange(
                                    defaultbillingAddress,
                                    "radio"
                                  )
                                }
                                htmlFor="1"
                                className="text-sm font-normal cursor-pointer text-black-300"
                              >
                                <div className="flex flex-col items-start gap-[7px]">
                                  <div className="flex flex-col space-y-px">
                                    <p className="font-semibold text-[#202124] text-sm leading-[22px]">
                                      {defaultbillingAddress?.firstName}
                                    </p>
                                    <p className="font-normal text-[#202124] text-sm leading-[22px]">
                                      {defaultbillingAddress?.address1},{" "}
                                      {defaultbillingAddress?.address2} ,{" "}
                                      {defaultbillingAddress?.city}-
                                      {defaultbillingAddress?.postcode} ,{" "}
                                      {defaultbillingAddress?.zone?.name} ,{" "}
                                      {hadleCounty(defaultbillingAddress)}
                                    </p>
                                    <p className="font-semibold text-[#202124] text-sm leading-[22px]">
                                      <span>{t("checkout.phone")}</span>
                                      <span>
                                        +91 {defaultbillingAddress?.phoneNo}
                                      </span>
                                    </p>
                                  </div>
                                </div>
                              </label>
                            </div>
                          </div>
                        </div>
                      ) : (
                        <></>
                      )
                    ) : (
                      <>
                        {billingAddressData != "" && (
                          <div className="p-[30px_20px] border border-[#E8E8E8] border-solid rounded-[12px]">
                            {billingAddressData?.map((data, i) => (
                              <div
                                className="flex justify-between items-start space-x-3 rtl:space-x-[unset] mb-[30px] last-of-type:mb-0 pb-[30px] last-of-type:pb-0 border-[#F4F4F4] border-b last-of-type:border-b-0 border-solid"
                                key={i}
                              >
                                <div className="flex items-start space-x-2 rtl:ml-3">
                                  <input
                                    id="1"
                                    onChange={() =>
                                      handleDefaultBillingAddressChange(
                                        data,
                                        "radio"
                                      )
                                    }
                                    type="radio"
                                    className="peer top-1 relative bg-white checked:!bg-[#000000] bg-contain bg-no-repeat checked:bg-none bg-center m-0 border !border-white checked:!border-white checked:border-[0.1875rem] rounded-full outline checked:outline outline-[#CCCCCC] outline-1 checked:!outline-[#000000] checked:outline-[2px] !outline-offset-0 focus:ring-0 w-[15px] checked:w-[16px] min-w-[18px] checked:min-w-[13px] h-[18px] checked:h-[16px] align-top appearance-none cursor-pointer print-color-auto"
                                    checked={
                                      data?.isDefault == 1 ? true : false
                                    }
                                  />

                                  <label
                                    onClick={() =>
                                      handleDefaultBillingAddressChange(
                                        data,
                                        "radio"
                                      )
                                    }
                                    htmlFor="1"
                                    className="text-sm font-normal cursor-pointer text-black-300"
                                  >
                                    <div className="flex flex-col items-start gap-[7px]">
                                      {data.isDefault == 1 && (
                                        <div className="flex justify-center items-center bg-[#ECF2FE] mb-[10px] px-[10px] rounded w-auto h-[27px] font-medium text-[#164092] text-[14px] leading-[17px]">
                                          {t("checkout.Default")}
                                        </div>
                                      )}

                                      <div className="flex flex-col space-y-px">
                                        <p className="font-semibold text-[#202124] text-sm leading-[22px]">
                                          {data?.firstName}
                                        </p>
                                        <p className="font-normal text-[#202124] text-sm break-all leading-[22px]">
                                          {data?.address1}, {data?.address2} ,{" "}
                                          {data?.city}-{data?.postcode} ,{" "}
                                          {data?.zone?.name} ,{" "}
                                          {hadleCounty(data)}
                                        </p>
                                        <p className="font-semibold text-[#202124] text-sm leading-[22px]">
                                          <span>{t("checkout.phone")}</span>
                                          <span>+91 {data?.phoneNo}</span>
                                        </p>
                                      </div>
                                    </div>
                                  </label>
                                </div>
                                <div className="flex flex-col space-y-6 max-w-[167px]">
                                  <div className="flex flex-row items-center justify-between gap-3">
                                    <a
                                      role="button"
                                      onClick={() => handleEdit(data)}
                                      className="relative flex items-center space-x-1 p-[8.5px_12px] border border-[#D9D9D9] border-solid rounded-[4px] min-w-[68px] font-normal text-[#202124] text-[14px] leading-none cursor-pointer"
                                    >
                                      <img src="/img/edit.svg" alt="edit" />
                                      <span> {t("myprofile.Edit")}</span>
                                    </a>
                                    <a
                                      role="button"
                                      onClick={() => handleDelete(data)}
                                      className="relative flex items-center space-x-1 p-[8.5px_12px] border border-[#D9D9D9] border-solid rounded-[4px] min-w-[83px] font-normal text-[#202124] text-[14px] leading-none cursor-pointer"
                                    >
                                      <img src="/img/delete.svg" alt="edit" />
                                      <span> {t("myprofile.Delete")}</span>
                                    </a>
                                  </div>
                                </div>
                              </div>
                            ))}
                          </div>
                        )}

                        {billingAddressData != "" ? (
                          <a
                            role="button"
                            className={`text-[#202124CC] text-[14px] flex justify-center gap-[10px] items-center font-medium leading-4 mt-[16px] capitalize cursor-pointer border border-[#C2C2C2] p-[14px] rounded-[8px] text-center border-dashed `}
                            onClick={() => setdelivery(1)}
                          >
                            <img src="/img/add.svg" alt="add" />
                            {t("checkout.NewAddr")}
                          </a>
                        ) : (
                          <a
                            role="button"
                            className={`text-[#202124CC] h-[235px] text-[18px] flex flex-col justify-center gap-[8px] items-center font-medium leading-[22px] capitalize cursor-pointer border border-[#C2C2C2] p-[14px] rounded-[8px] text-center border-dashed `}
                            onClick={() => setdelivery(1)}
                          >
                            <img src="/img/add-new-address.svg" alt="add" />
                            {t("checkout.NewAddr")}
                          </a>
                        )}
                      </>
                    )}
                  </Fragment>
                )}
              </div>
            )}

            {/* Price Details */}
            <CheckoutPriceDetails
              token={token}
              handlePayNow={handlePayNow}
              cartPath={cartPath}
              addonlist={addonlist}
              loader={loader}
              valid={
                enableBackOrder && defaultShippingAddress
                  ? true
                  : enableBackOrder == false &&
                    defaultShippingAddress &&
                    defaultbillingAddress
                  ? true
                  : false
              }
            />
          </div>
        </div>
      ) : (
        <AddNewAddress
          setDelivery={setDelivery}
          addAddress={addAddress}
          setAddress={setAddress}
          validecheck={validecheck}
          setValideCheck={setValideCheck}
          countryOption={countryOption}
          country={country}
          stateOptions={stateOptions}
          state={state}
          setState={setState}
          defalut={defalut}
          setDefault={setDefault}
          handleAddressSubmit={handleAddressSubmit}
          setCountry={setCountry}
        />
      )}

      <Contactus token={token} />
    </>
  );
}
