"use client";

// Components Imports
import DeliveryAddress from "./DeliveryAddress";
import DeliveryBillingAddress from "./DeliveryBillingAddress";

export default function CheckoutAddress({
  token,
  countryList,
  stateList,
  addonlist,
}) {
  return token ? (
    <DeliveryBillingAddress
      token={token}
      countryList={countryList}
      stateList={stateList}
      addonlist={addonlist}
    />
  ) : (
    <DeliveryAddress
      token={token}
      countryList={countryList}
      stateList={stateList}
      addonlist={addonlist}
    />
  );
}
