"use client";

// Component
import { useTranslation } from "react-i18next";

//3rd Party
import Select from "react-select";

const AddNewAddress = ({
  setDelivery,
  addAddress,
  setAddress,
  validecheck,
  setValideCheck,
  countryOption,
  country,
  stateOptions,
  state,
  setState,
  defalut,
  setDefault,
  handleAddressSubmit,
  setCountry,
}) => {
  const { t } = useTranslation();

  const handleinduscountry = (e) => {
    setState("");
    setCountry(e);
  };

  const handleChange = (e) => {
    const { name, value } = e.target;
    if (name === "number") {
      let regex = new RegExp(/[^0-9]/g);
      if (!value.match(regex)) {
        if (value.length < 16) {
          setAddress((pre) => ({
            ...pre,
            [name]: value?.trimStart(),
          }));
        }
      }
    } else if (name == "pincode") {
      let regex = new RegExp(/[^0-9]/g);
      if (!value.match(regex)) {
        if (value.length < 7) {
          setAddress((pre) => ({
            ...pre,
            [name]: value?.trimStart()?.toString(),
          }));
        }
      }
    } else {
      setAddress((pre) => ({
        ...pre,
        [name]: value?.trimStart(),
      }));
    }
  };

  // Design

  const colourStylesError = {
    control: (styles, state) => ({
      ...styles,
      cursor: "pointer",
      borderRadius: "0.25rem",
      height: "2.75rem",
      boxShadow: state.isFocused ? 0 : 0,
      borderColor: state.isFocused
        ? validecheck && country == ""
          ? "red"
          : "#BEC0C5"
        : validecheck && country == ""
        ? "red"
        : "#BEC0C5",
      "&:hover": {
        borderColor: state.isFocused
          ? "#BEC0C5"
          : validecheck && country == ""
          ? "red"
          : "#BEC0C5",
      },
    }),

    option: (base, state) => ({
      ...base,
      cursor: "pointer",
      backgroundColor: state.isSelected ? "#ffffff" : "#ffffff",
      "&:hover": { backgroundColor: "#F9F9FC" },
      fontSize: ".8125rem",
      padding: "8px",
      color: "#333333",
    }),
  };

  const colourStylesErrorState = {
    control: (styles, statelist) => ({
      ...styles,
      cursor: "pointer",
      borderRadius: "0.25rem",
      height: "2.75rem",
      boxShadow: statelist.isFocused ? 0 : 0,
      borderColor: statelist.isFocused
        ? validecheck && state == ""
          ? "red"
          : "#BEC0C5"
        : validecheck && state == ""
        ? "red"
        : "#BEC0C5",
      "&:hover": {
        borderColor: statelist.isFocused
          ? "#BEC0C5"
          : validecheck && state == ""
          ? "red"
          : "#BEC0C5",
      },
    }),

    option: (base, statelist) => ({
      ...base,
      cursor: "pointer",
      backgroundColor: statelist.isSelected ? "#ffffff" : "#ffffff",
      "&:hover": { backgroundColor: "#F9F9FC" },
      fontSize: ".8125rem",
      padding: "8px",
      color: "#333333",
    }),
  };

  return (
    <div
      className="m-auto mb-20 px-4 sm:px-10 py-4 max-w-screen-2xl"
      data-twe-input-wrapper-init
    >
      <div className="mx-auto max-w-[825px]">
        <h3 className="mb-[10px] p-[20px_0] font-semibold text-[#202124] text-[20px] leading-[24px]">
          {t("checkout.AddDelivAddr")}
        </h3>
        <div className="gap-x-[20px] gap-y-[25px] grid grid-cols-2 max-sm:grid-cols-1">
          <div>
            <input
              type="text"
              placeholder={t("signin.FirstName")}
              value={addAddress?.name?.trimStart()}
              name="name"
              maxLength={32}
              onChange={handleChange}
              id="exampleFormControlInput2"
              className={`p-[15.5px_20px] border focus:!border-[#BEC0C5] border-solid rounded-[8px] w-full h-[48px] font-normal text-[#202124] text-[14px] placeholder:text-[#737782] ${
                validecheck && addAddress.name == ""
                  ? "border-[#ff0000]"
                  : "border-[#BEC0C5]"
              }`}
            />
            {validecheck && addAddress.name == "" && (
              <p className="mt-1 font-normal text-red-invalid text-xs">
                {t("checkout.Entname")}
              </p>
            )}
          </div>

          <div>
            <input
              type="text"
              id="exampleFormControlInput3"
              placeholder={t("signin.LastName")}
              value={addAddress?.lname}
              name="lname"
              maxLength={32}
              onChange={handleChange}
              className="p-[15.5px_20px] border border-[#BEC0C5] focus:!border-[#BEC0C5] border-solid rounded-[8px] w-full h-[48px] font-normal text-[#202124] text-[14px] placeholder:text-[#737782]"
            />
          </div>

          <div className="col-span-2 max-sm:col-span-1">
            <input
              type="text"
              id="exampleFormControlInput9"
              placeholder={t("checkout.Mobile")}
              value={addAddress?.number}
              name="number"
              onChange={handleChange}
              className={`p-[15.5px_20px] border focus:!border-[#BEC0C5] border-solid rounded-[8px] w-full h-[48px] font-normal text-[#202124] text-[14px] placeholder:text-[#737782] ${
                validecheck && addAddress.number == ""
                  ? "border-[#ff0000]"
                  : "border-[#BEC0C5]"
              }`}
            />
            {validecheck && addAddress.number == "" && (
              <p className="mt-1 font-normal text-red-invalid text-xs">
                {t("checkout.EntNum")}
              </p>
            )}
          </div>

          <div className="col-span-2 max-sm:col-span-1">
            <textarea
              className={`p-[15.5px_20px] border border-[#BEC0C5] focus:!border-[#BEC0C5] border-solid rounded-[8px] w-full h-[119px] font-normal text-[#202124] text-[14px] placeholder:text-[#737782] resize-none ${
                validecheck && addAddress?.address == ""
                  ? "border-[#ff0000]"
                  : "border-[#BEC0C5]"
              }`}
              id="exampleFormControlInput4"
              placeholder={t("checkout.StrAddr")}
              value={addAddress?.address}
              name="address"
              maxLength={128}
              onChange={handleChange}
            ></textarea>
            {validecheck && addAddress?.address == "" && (
              <p className="mt-1 font-normal text-red-invalid text-xs">
                {t("checkout.youraddr")}
              </p>
            )}
          </div>

          <div className="cursor-pointer">
            <Select
              placeholder={t("myprofile.Country")}
              name="select"
              options={countryOption}
              onChange={(e) => handleinduscountry(e)}
              styles={colourStylesError}
              value={country}
              className={` block w-full px-0 mt-0 bg-transparent border-0  appearance-none z-1 focus:outline-none focus:ring-0 focus:border-black border-gray-200 cursor-pointer ${
                validecheck == true && country == "" && "border-valid"
              }`}
              isClearable
            />
            {validecheck && country == "" && (
              <p className="mt-1 font-normal text-red-invalid text-xs">
                {t("checkout.SelCountry")}
              </p>
            )}
          </div>

          <div className="cursor-pointer">
            <Select
              name="select"
              options={stateOptions}
              styles={colourStylesErrorState}
              onChange={(e) => {
                setState(e);
              }}
              value={state}
              className={` block w-full px-0 mt-0 bg-transparent border-0  appearance-none z-1 focus:outline-none focus:ring-0 focus:border-black border-gray-200 cursor-pointer ${
                validecheck == true && state == "" && "border-valid"
              }`}
              placeholder={t("myprofile.State")}
            />
            {validecheck && state == "" && (
              <p className="mt-1 font-normal text-red-invalid text-xs">
                {t("checkout.State")}
              </p>
            )}
          </div>

          <div>
            <input
              type="text"
              id="exampleFormControlInput6"
              placeholder={t("myprofile.City")}
              value={addAddress?.city}
              name="city"
              maxLength={128}
              onChange={handleChange}
              className={`p-[15.5px_20px] border border-[#BEC0C5] focus:!border-[#BEC0C5] border-solid rounded-[8px] w-full h-[48px] font-normal text-[#202124] text-[14px] placeholder:text-[#737782] ${
                validecheck && addAddress?.city == ""
                  ? "border-[#ff0000]"
                  : "border-[#BEC0C5]"
              }`}
            />

            {validecheck && addAddress?.city == "" && (
              <p className="mt-1 font-normal text-red-invalid text-xs">
                {t("checkout.Entcity")}
              </p>
            )}
          </div>

          <div>
            <input
              type="text"
              id="exampleFormControlInput7"
              placeholder={t("checkout.EntPin")}
              value={addAddress?.pincode}
              name="pincode"
              onChange={handleChange}
              className={`p-[15.5px_20px] border border-[#BEC0C5] focus:!border-[#BEC0C5] border-solid rounded-[8px] w-full h-[48px] font-normal text-[#202124] text-[14px] placeholder:text-[#737782] ${
                validecheck && addAddress?.pincode == ""
                  ? "border-[#ff0000]"
                  : "border-[#BEC0C5]"
              }`}
            />

            {validecheck && addAddress?.pincode == "" && (
              <p className="mt-1 font-normal text-red-invalid text-xs">
                {t("checkout.EntPin")}
              </p>
            )}
          </div>

          <div className="col-span-2 max-sm:col-span-1">
            <input
              type="text"
              id="exampleFormControlInput5"
              placeholder={t("checkout.optional")}
              value={addAddress?.address2}
              name="address2"
              maxLength={128}
              onChange={handleChange}
              className={`p-[15.5px_20px] border border-[#BEC0C5] focus:!border-[#BEC0C5] border-solid rounded-[8px] w-full h-[48px] font-normal text-[#202124] text-[14px] placeholder:text-[#737782] ${
                validecheck && addAddress?.address2 == ""
                  ? "border-[#ff0000]"
                  : "border-[#BEC0C5]"
              }`}
            />

            {validecheck && addAddress?.address2 == "" && (
              <p className="mt-1 font-normal text-red-invalid text-xs">
                {t("checkout.youraddr")}
              </p>
            )}
          </div>

          <div className="flex items-center space-x-2">
            <label
              htmlFor="3"
              className="flex items-center gap-[10px] font-normal text-[#202124] text-base cursor-pointer"
            >
              <input
                id="3"
                type="checkbox"
                className="peer hidden border-grey-400 rounded-md focus:ring-0 focus:ring-transparent w-8 h-8 text-white cursor-pointer checkbox"
                onChange={(e) => setDefault(e.target.checked)}
                checked={defalut}
              />
              <img
                src="/img/unchecked.svg"
                alt=""
                className="peer-checked:hidden block"
                height={"18px"}
                width={"18px"}
              />
              <img
                src="/img/checked.svg"
                alt=""
                className="hidden peer-checked:block"
                height={"18px"}
                width={"18px"}
              />
              {t("checkout.Setdefault")}
            </label>
          </div>

          <div className="flex justify-end items-center gap-[8px] col-span-2 max-sm:col-span-1">
            <a
              role="button"
              onClick={() => {
                setDelivery(false);
                setValideCheck(false);
              }}
              className="bg-[#FFFFFF] p-[14px_24px] border border-[#20212452] border-solid rounded-[4px] w-[140px] font-medium text-[#202124CC] text-[14px] text-center leading-[17px] whitespace-nowrap cursor-pointer"
            >
              {t("checkout.Cancel")}
            </a>
            <a
              role="button"
              onClick={handleAddressSubmit}
              className="bg-[#202124] p-[14px_24px] border border-[#202124] border-solid rounded-[4px] w-[140px] font-medium text-[#FFFFFF] text-[14px] leading-[17px] whitespace-nowrap cursor-pointer"
            >
              {t("checkout.Save")}
              {/* {t("forgotpassword.Submit")} */}
            </a>
          </div>
        </div>
      </div>
    </div>
  );
};

export default AddNewAddress;
